<template>
  <div class="chMedicine">
    <medicine-options @search="search" ref="medicOptions"/>
    <medicine-table :medTotalSize="totalSize" :loading="loading" @search="search" :data="data" :pageShow="pageShow" ref="medicTable"/>
  </div>
</template>

<script>
import MedicineOptions from './controls/medicineOptions'
import MedicineTable from './controls/medicineTable'
import queryString from "querystring"

export default {
  name: 'chMedicine',
  data() {
    return {
      data:[], //table数据
      totalSize: 0, //一共多少条数据
      pageShow: false,  //页码切换是否显示
      loading: false
    }
  },
  components: {
    MedicineOptions,
    MedicineTable
  },
  mounted(){
    this.search();
  },
  methods:{
    search(){  //搜索
      this.loading = true;
      const optionParams = this.$refs.medicOptions.getData();
      const tableParams = this.$refs.medicTable.getData();
      let params = this.lodash.assign({}, optionParams, tableParams);
      const param = queryString.stringify(params || {});
      this.api.get(`/chinaMed?${param}`).then((res)=>{
        this.loading = false;
        if(!res.data) return;
        this.data = res.data.list;
        this.totalSize = res.data.totalPage;
        this.pageShow = this.data.length > 20
      }).catch((err) => {
        console.log(err)
        this.$message(err.message)
      })
    }
  }
}
</script>
 
